import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, ViewController } from 'ionic-angular';

import { Sound } from '../../providers/sound';

@IonicPage()
@Component({
  selector: 'page-detail-item',
  templateUrl: 'detail-item.html'
})

export class DetailItem {
	id_produk : string;
	item : string;
	harga : number;
	jumlah : number;
  modal:number;
	total : number;
	batas : boolean = true;
  icon : string='star-outline';
  favoritCheck : boolean = false;

  constructor(
  	public navCtrl: NavController, 
  	public navParams: NavParams,
    public sound:Sound,
  	public alertCtrl: AlertController,
  	public viewCtrl: ViewController) {

  	this.id_produk = this.navParams.get('id');
  	this.item = this.navParams.get('item');
  	this.harga = this.navParams.get('harga');
    this.modal = this.navParams.get('modal');
  	this.total = this.harga;
  	this.jumlah = 1;
    this.checkFavorit();
  }


  goBack(){
    this.sound.play('tabSwitch');
  	this.navCtrl.pop();
  }

  checkFavorit(){
    let check = JSON.parse(localStorage.getItem('favorit'));
    for (var i in check) {
      if(check[i].id == this.id_produk){
        this.icon = "star";
        this.favoritCheck = true;
      }
    }
  }


  favorit(){
    this.sound.play('tabSwitch');
    let favorit = {
      id: this.id_produk,
      item: this.item,
      harga: this.harga
    }

    if (!this.favoritCheck){
      let ambil = JSON.parse(localStorage.getItem('favorit'));
      if (ambil == null || ambil.length == 0){
        localStorage.setItem('favorit', JSON.stringify([favorit]));
        this.favoritCheck = true;
      }
      else {
        for (var i in ambil) {
          if (ambil[i].id == this.id_produk){
            ambil.splice(i,1);
            ambil.push(favorit);
            this.favoritCheck = true;
          }
          else {
            ambil.push(favorit);
            localStorage.setItem('favorit', JSON.stringify(ambil));
            this.favoritCheck = true;
            break;
          }
        } 
      }   
      this.alertFavorit();
      this.icon = 'star';  
    }
  }

  alertFavorit(){
    let alert = this.alertCtrl.create({
      subTitle: 'terima Kasih, Item Telah Ditambhkan ke list Favorit',
      buttons: ['ok Bro']
    });
    alert.present();
  }


  minusJumlah(){
    this.sound.play('tabSwitch');
  	if (this.jumlah == 2){
  		this.batas = true;
  	}
  	this.jumlah--;
  	this.total = this.total - this.harga;
  }

  plusJumlah(){
    this.sound.play('tabSwitch');
  	if (this.jumlah >= 1){
  		this.batas = false;
  	}
  	this.jumlah++;
  	this.total = this.jumlah * this.harga;
  }

  addToCart(){
    this.sound.play('tabSwitch');
  	let cart = {
		  id: this.id_produk,
			item: this.item,
			jumlah: this.jumlah,
      modal:this.modal,
			total: this.total
  	};

  	if(!localStorage.getItem('cart')){
  		localStorage.setItem('cart',JSON.stringify([cart]));
  	}
  	else {
  		let ambil = JSON.parse(localStorage.getItem('cart'));
  			
  		for (var i in ambil) {
  			if (ambil[i].id === this.id_produk){
  				ambil.splice(i,1);
  			}
  		}

  		ambil.push(cart);
  		localStorage.setItem('cart', JSON.stringify(ambil));
  	}

  	let confirm = this.alertCtrl.create({
  		message: 'Terima Kasih, Pesanan anda telah ditambahkan ke keranjang',
  		buttons: [
  			{
  				text: 'Pesan lagi',
  				handler: () => {
  					this.toHome();
  				}
  			},
  			{
  				text: 'Bayar',
  				handler: () => {
  					this.bayar();
  				}
  			}
  		]
  	});
  	confirm.present();
  }

  toHome(){
    this.sound.play('tabSwitch');
  	this.navCtrl.popToRoot();
  }

  bayar(){
    this.sound.play('tabSwitch');
  	this.navCtrl.push('Cart');
  }

}



